@font-face {
  font-family: 'RobotoMono';
  src: url(/react-calculator/build/static/media/Roboto-Thin.89e2666c.ttf);
}

* {
  font-family: 'RobotoMono', monospace;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
  color: #FFFFFF;
  background: radial-gradient(circle, rgb(83,105,118) 30%, rgb(41,46,73));
}
/* 
    This project is based on a calculator from MacOS
    The same height and width
*/

.calculator {
    height: 323px;
    width: 234px;
    border-radius: 5px;
    /* overflow: hidden; */
    cursor: pointer;
    display: grid;
    grid-template-columns: repeat(4, 25%);
    /* 3rem = 48px */
    grid-template-rows: 1fr repeat(5, 3rem); 
    box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.3);

}
:root {
    --bg-button: #5E6065;
    --border-button: solid 1px #2B2D36;
    --button-number-color: #E7E7E8;
}

.button {
    font-size: 1.4em;
    background-color: #5E6065;
    background-color: var(--bg-button);
    border: none;
    border-right: solid 1px #2B2D36;
    border-right: var(--border-button);
    border-bottom: solid 1px #2B2D36;
    border-bottom: var(--border-button);
    margin: 0;
    outline: none;
    color: #E7E7E8;
    color: var(--button-number-color);
    cursor: pointer;

}

.button:active {
    background-color: #ccc;
}

.button.double {
    grid-column: 1 / 3;
}

.button.triple {
    grid-column: 1 / 4;
}

.button.operation {
    background-color: #FF9F09;
}

.button.operation:active {
    background-color: #FF9F09cc;
}

.button.operation:focus {
    border: 2px solid #2B2D37;
}

.button.c {
    background-color: #454547;
}

.button.c:active {
    background-color: #454547cc;
}
:root {
    --bg-display: #2B2D37;
}

.display {
    border: var(--border-button);
    padding-right: 15px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    font-size: 50px;
    letter-spacing: -1px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    grid-column: 1 / 5;
    background-color: #2B2D37;
    background-color: var(--bg-display);
    height: 82px;
    width: 234;
    overflow: hidden;
}
